.hoveractivity .hover-area .icon-home {
	 width: 80px;
	 height: 80px;
	 position: absolute;
	 bottom: 91px;
	 left: calc(50% - 40px);
	 transform-origin: bottom center;
	 transform-style: preserve-3d;
	 transform: scale(0);
	 transition: transform 225ms ease-out;
}
.hoveractivity .hover-area .icon-home .roof {
	 height: 90px;
	 position: absolute;
	 left: 50%;
	 transform-origin: bottom center;
	 transform-style: preserve-3d;
	 transform: translateZ(45px) rotateX(90deg);
}
.hoveractivity .hover-area .icon-home .roof.roof.roof-1 {
	 top: -90px;
}
.hoveractivity .hover-area .icon-home .roof.roof.roof-2 {
	 top: -110px;
}
.hoveractivity .hover-area .icon-home .roof:before, .hover-area .icon-home .roof:after {
	 content: '';
	 background-color: #cfa682;
	 width: 70px;
	 height: 100%;
	 position: absolute;
	 top: 0;
	 transform-style: preserve-3d;
}
.hoveractivity .hover-area .icon-home .roof:before {
	 right: 0;
	 transform-origin: bottom right;
	 transform: rotateY(-37.5deg);
	 filter: brightness(105%);
}
.hoveractivity .hover-area .icon-home .roof:after {
	 left: 0;
	 transform-origin: bottom left;
	 transform: rotateY(37.5deg);
	 filter: brightness(95%);
}
.hoveractivity .hover-area .icon-home .roof-design {
	 height: 20px;
	 position: absolute;
	 top: -20px;
	 left: 50%;
	 transform-style: preserve-3d;
}
.hoveractivity .hover-area .icon-home .roof-design.roof-design-1 {
	 transform: translateZ(45px);
}
.hoveractivity .hover-area .icon-home .roof-design.roof-design-2 {
	 transform: translateZ(-45px);
}
.hoveractivity .hover-area .icon-home .roof-design:before, .hover-area .icon-home .roof-design:after {
	 content: '';
	 background-color: #cfa682;
	 width: 55px;
	 height: 20px;
	 position: absolute;
	 top: 0;
	 transform-style: preserve-3d;
}
.hoveractivity .hover-area .icon-home .roof-design:before {
	 right: 0;
	 transform-origin: bottom right;
	 transform: skewY(-37.5deg);
}
.hoveractivity .hover-area .icon-home .roof-design:after {
	 left: 0;
	 transform-origin: bottom left;
	 transform: skewY(37.5deg);
}
.hoveractivity .hover-area .icon-home .roof-side {
	 background-color: #cfa682;
	 width: 90px;
	 height: 20px;
	 position: absolute;
	 top: 22px;
	 transform-origin: top left;
	 transform-style: preserve-3d;
	 transform: translateZ(45px) rotateY(90deg);
}
.hoveractivity .hover-area .icon-home .roof-side.roof-side-1 {
	 left: -15px;
	 filter: brightness(105%);
}
.hoveractivity .hover-area .icon-home .roof-side.roof-side-2 {
	 right: -105px;
	 filter: brightness(95%);
}
.hoveractivity .hover-area .icon-home .roof-wall {
	 border-top: none;
	 border-bottom: solid 31px #ebbb8e;
	 border-left: solid 40px transparent;
	 border-right: solid 40px transparent;
	 position: absolute;
	 top: 0;
	 left: 0;
	 transform-style: preserve-3d;
}
.hoveractivity .hover-area .icon-home .roof-wall.roof-wall-1 {
	 transform: translateZ(40px);
}
.hoveractivity .hover-area .icon-home .roof-wall.roof-wall-2 {
