  }
  
  .data-exercise .domain {
    display: none;
  }
  
  .data-exercise line {
    display: none;
  }
  
  .data-exercise text {
    cursor: default;
  }
  
  .data-exercise .leisure,
  .data-exercise .work {
    fill: black;
    transition: fill 500ms ease;
    cursor: default;
  }
  
  .data-exercise .leisure:hover,
  .data-exercise .work:hover {
    fill: white;
  }
  
  .data-exercise button {
    border: 2px solid black;
    background-color: white;
    padding: 20px;
  }
  
  .data-exercise button:hover {
    border: 2px solid black;
    background-color: gray;
    cursor: pointer;
  }
  
.data-visualization {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    cursor: url("http://egretegret.github.io/cursor.png"), auto;
}

.data-visualization .title {
    font-size: 64px;
    text-align: left;
    max-width: 250px;
    font-family: 'Montserrat', sans-serif;
    padding-left: 5px;
}

.data-visualization .time-slider {
    height: 50vh;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.data-visualization .time-slider .time-slider-line {
    height: 10px;
    width: 100%;
    background-color: black;
    /* margin-top: 30px;
    margin-bottom: 30px;
    background-clip: border-box; */
    z-index: 3;
}

.data-visualization .time-slider .time-slider-line:hover {
    background-color: grey;
}

.data-visualization .time-slider .time-slider-day {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    font-family: 'Yuji Syuku', serif;
    font-size: 84px;
    z-index: 1;
}

.data-visualization .data {
    position: absolute;
    top: 0;
    left: 250px;
    right: 250px;
    width: calc(100% - 500px);
    height: 100%;
    /* display: none; */
}

.data-visualization img:hover {
    cursor: url("http://egretegret.github.io/pointer.png"), pointer;
}

@media screen and (min-width: 3000px) and (min-height: 3000px) {
    .data-visualization img {
        position: absolute;
        max-width: 150px;
        max-height: 150px;
    }
  }

@media screen and (max-width: 2999px) and (max-height: 2999px) {
    .data-visualization img {
        position: absolute;
        max-width: 100px;
        max-height: 100px;
    }
}

